import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';

const TextComp = ({ text }) => (
    <div style={{
        color: 'white', 
        background: 'black',
        padding: '15px 10px',
        display: 'inline-flex',
        textAlign: 'center',
        alignItems: 'center',
        borderRadius: '100%',
        transform: 'translate(-50%, -50%)'
    }}>
        {text}
    </div>
    );

export default class SimpleMap extends Component {
    static defaultProps = {
        center: {lat: 40.6441825, lng: -74.0474019},
        zoom: 11
    };

    render() {
        return (
            <GoogleMapReact
                defaultCenter={this.props.center}
                defaultZoom={this.props.zoom}>
                <TextComp 
                    lat={40.6441825}
                    lng={-74.0474019} 
                    text={'Brooklyn, New York'} 
                    />
            </GoogleMapReact>
        );
    }
}
  


