import React, { Component } from 'react';
export default class About2 extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="about2">  

        <div class="row section-intro">
          <div >

            {/* <h5>About</h5> */}
            <h1>Let me introduce myself</h1>
            
            <div class="intro-info">
              <img class="profile-pic" src="images/dp.jpg" alt="Profile Picture"/>
              <p>{resumeData.aboutme}</p>
            </div>   			

          </div>   		
        </div> 

        <div class="row about2-content">
          <div class="col-six tab-full">

            <h3>Profile</h3>
            <p>{resumeData.role}</p>

            <ul class="info-list">
              <li>
                <strong>Fullname:</strong>
                <span>{resumeData.name}</span>
              </li>
              <li>
                <strong>Birth Date:</strong>
                <span>{resumeData.dob}</span>
              </li>
              <li>
                <strong>Job:</strong>
                <span>{resumeData.role}</span>
              </li>
              <li>
                <strong>Website:</strong>
                <span>{resumeData.imagebaseurl}</span>
              </li>
              <li>
                <strong>Email:</strong>
                <span>{resumeData.email}</span>
              </li>

            </ul> 

          </div>

          <div class="col-six tab-full">

            <h3>Work Experience</h3>
            <p>{resumeData.primary}</p>

            <ul class="skill-bars">
              <li>
                <div class="progress percent90"><span>75%</span></div>
                <strong>JAVA - Android</strong>
              </li>
              <li>
                <div class="progress percent85"><span>70%</span></div>
                <strong>Python - Django</strong>
              </li>
              <li>
                <div class="progress percent70"><span>70%</span></div>
                <strong>AWS - S3, EC2, SQS, RDS</strong>
              </li>
              <li>
                <div class="progress percent95"><span>60%</span></div>
                <strong>SQL - Postgres, MySql, NoSql</strong>
              </li>
              <li>
                <div class="progress percent75"><span>80%</span></div>
                <strong>Team Management and Problem Solving</strong>
              </li>
          
            </ul> 
          </div>

        </div>

        <div class="row button-section">
          <div class="col-twelve">
            {/* <a href="#contact" title="Hire Me" class="button stroke smoothscroll">Hire Me</a> */}
            <a href={resumeData.cv} target="_blank" title="Download CV" class="button button-primary">Download CV</a>
          </div>   		
        </div>
      </section> 
    );
  }
}



