import React, { Component } from 'react';
export default  class Resume2 extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
            <section id="resume2" class="grey-section">
            <div class="row section-intro">
                    <div class="col-twelve">
                        <h5>Resume</h5>
                        <p>Courses featuring in programming assignments coupled with my Final year project - 'Guitar Tablature' 
                             at Mit-Pune got me interested in coding which later persuaded me to make a tuff 
                            decision of joining a Silicon Valley Funded Startup 'Shoptimize' as a Software Development 
                            Engineer, over my on-campus placement as Embedded Engineer at a multi national company 
                            'Wipro Limited'. Designing, developing and deploying Web/Mobile Apps honed my programming 
                            skills and played a significant role in my decision to pursue Masters from NYU.</p>

                    </div>   		
                </div> 
                <div class="row resume2-timeline">
                        <div class="col-twelve resume2-header">
                            <h2>Education</h2>
                        </div> 

                        <div class="col-twelve">
                            <div class="timeline-wrap">{
                                resumeData.education_data && resumeData.education_data.map((item)=>{
                                return(
                                    <div class="timeline-block">
                                        <div class="timeline-ico">
                                            <i class="fa fa-star"></i>
                                        </div>
                                        <div class="timeline-header">
                                            <h3>{item.degree}</h3>
                                            <p>{item.month_passing} {item.year_passing}</p>
                                        </div>
                                        <div class="timeline-content">
                                            <a href={item.link} target="_blank"><h4>{item.university_name}</h4></a>
                                            <div class="intro-info">
                                                <img className="profile-pic" src={item.image} alt="" />
                                                <p class="lead">{item.Achievements}</p><br></br>
                                            </div>  
                                            <div class="courses">{
                                                item.courses && item.courses.map((list)=>{
                                                    return( 
                                                        <p>{list.name}</p>
                                                    )})
                                            }
                                            </div>
                                        </div>
                                    </div> 
                                    )})
                                }
                            </div>  			
                        </div> 
                </div>

            </section>

    );
  }
}



