import React, { Component } from 'react';
import SimpleMap from '../simplemap/simplemap';

export default class ContactUs extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="contact">
          <div className="row section-head">
            <div className="ten columns">
              <p className="lead">
              Feel free to contact me as per convenience...
              </p>
            </div>
          </div>
          <div className="row">
            <aside className="eigth columns footer-widgets">
              
              <div className="widget">
                <h4>Phone :{resumeData.phone}</h4>
              </div>
              <div className="widget">
                <h4>Address :{resumeData.address}</h4>
              </div>
              <div className="widget">
                <h4>Email :{resumeData.email}</h4>
              </div>
              <div className="widget">
                <h4>Linked in :{resumeData.linkedinId}</h4>
              </div>
            </aside>
          </div>
          <div className="row" style={{width: '70%', height: '400px'}}>
            <SimpleMap/>
          </div>
        </section>
        );
  }
}



